@import url(http://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,800,900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body {
    min-height: 100vh;
    overflow-x: hidden;
}
.cc__logo {
  width: 250px;
}

.cc__container {
  text-align: center;
  padding: 40px;
  -webkit-flex: 0.4 1;
          flex: 0.4 1;
  box-shadow: 0px 0px 20px #efecec;
  margin: 20px 20px;
  border-radius: 20px;
}

.cc__rating {
  padding: 5px 20px;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
  margin: 40px auto;
  border-radius: 5px;
  background: #b39b8c;
  color: white;
}
.graphs {
  display: -webkit-flex;
  display: flex;
  margin: 40px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.container {
  display: -webkit-flex;
  display: flex;
  margin: 40px 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.cf__logo {
  width: 250px;
  margin-top: 48px;
}
.cf__rating {
  padding: 5px 20px;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
  margin: 40px auto;
  border-radius: 5px;
  background: #8fcbea;
  color: white;
}

.circular {
  width: 150px;
  border-radius: 100%;
  height: 150px;
  border: 10px solid bisque;
  background: #ffb282;
  color: white;
  margin: 10px;
  box-shadow: 2px 2px 10px #d4d4d4;
}
.circular__h2{
  padding: 45px 0 0 0;
}

.circular__p {
  font-size: 12px;
}

.cc__info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;

}

#solveToday {
  background: #aefdc8;
  border: 10px solid #e3fde3;
}

#submitToday {
  background: #add9ff;
  border: 8px solid aliceblue;
}

#fullSolve {
  background: #d6acfb;
  border: 10px solid #f3e5ff;
}

#partSolve {
  border: 10px solid #fde4ef;
  background: #ffb2ce;
}
.graph__container {
  width:40%;
  margin: 20px;
}

.form__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 40px;
  box-shadow: 0px 0px 20px #eaeaea;
  border-radius: 10px;
}
.form > h2 {
  margin-bottom: 20px;
}
.form > input[type=text] {
  padding: 20px 10px;
  border-radius: 8px;
  margin: 10px 0;
}
.form > button:hover {
  background: #ee7c36 !important;
  cursor: pointer;
}
.form > button {
  padding: 10px;
  background: #ffb282;
  font-weight: 600;
  border: 0;
  color: white;
  border-radius: 8px;
  font-size: 18px;
}
.loader__container {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.load__text {
  margin-top: 40px;
  margin-left: 35px;
}
@media only screen and (max-width: 1200px){
  .graph__container {
    width:100%;
  }
}


